import React from 'react';
import './App.css';
import { Card, Button, Empty, Divider, InputNumber, Input } from 'antd';
import { PlusOutlined, DeleteOutlined, CloseOutlined } from '@ant-design/icons';
import toast, { ToastBar, Toaster } from 'react-hot-toast';


function App() {
  function generateUUID() { // Public Domain/MIT
    var d = new Date().getTime();//Timestamp
    var d2 = ((typeof performance !== 'undefined') && performance.now && (performance.now()*1000)) || 0;//Time in microseconds since page-load or 0 if unsupported
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random() * 16;//random number between 0 and 16
        if(d > 0){//Use timestamp until depleted
            r = (d + r)%16 | 0;
            d = Math.floor(d/16);
        } else {//Use microseconds since page-load if supported
            r = (d2 + r)%16 | 0;
            d2 = Math.floor(d2/16);
        }
        return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
}



  const [products, setProducts] = React.useState([])
  const [productCreator, createNewProduct] = React.useState(false)
  const [finalView, setView] = React.useState(false)

  const [productName, setProductName] = React.useState('')
  const [productPrice, setProductPrice] = React.useState(100)
  const [totalPrice, setTotalPrice] = React.useState(0);

  return (
    <>
      <Toaster>
        {(t) => (
          <ToastBar toast={t}>
            {({ icon, message }) => (
              <>
                {icon}
                <p className='font-inter m-0'>{message}</p>
              </>
            )}
          </ToastBar>
        )}
      </Toaster>;
      <div style={{backgroundColor: '#F1f1f1', color: '#252525', display: 'flex', justifyContent: "center", alignItems: 'start', height: '100vh', width: '100%'}}>
        <div style={{height: '100vh'}} className='flex-row justify-center align-middle text-center'>
        <h1 className='font-semibold font-inter text-4xl'>Volaria Customs</h1>
        <h1 className='font-light font-inter text-2xl'>Price Calculation System</h1>
        {finalView === false ? (
        <div>
          <div className='flex items-center justify-start flex-wrap'>
            <h1 className='font-semibold font-inter text-2xl'>Products</h1>
            <Button onClick={() => {createNewProduct(true);}} disabled={productCreator} className='ml-auto' shape="circle" type='dashed' icon={<PlusOutlined />}/>
          </div>
          <Divider style={{margin: '0', marginBottom: '1rem', marginTop: '1rem '}}/>
          <div className='flex flex-col items-center justify-center text-left'>
            {productCreator === true && (
              <Card actions={[<PlusOutlined onClick={() => {
                if(productName !== '' && productPrice !== null) {
                  setProducts([...products, {name: productName, price: productPrice, id: generateUUID()}])
                  createNewProduct(false)
                  toast.success(`${productName} was added to the receipt!`);
                  setProductName('')
                  setProductPrice(100)
                }
              }} key='create'/>, <CloseOutlined onClick={() => {              
                createNewProduct(false)
                setProductName('')
                setProductPrice(100)
                }} key='cancel'/>]} type='inner' style={{width: '100%'}} >
              <div className='flex flex-col'>
                <p className='font-inter text-xl font-bold m-0 mb-3'>New Product</p>
                <Input onChange={(e) => {setProductName(e.target.value)}} value={productName} placeholder='Product Name' style={{ width: 240 }}/>
                <InputNumber onChange={(e) => {setProductPrice(e)}} value={productPrice} className='mt-3' addonBefore="R$" defaultValue={100} />
              </div>
              </Card>
            )}
            {products.length === 0 && productCreator === false? <Empty /> : products.map((product) => (
              <Card className='mt-5' actions={[<DeleteOutlined onClick={() => {
                toast.success(`${product.name} was removed from the receipt!`);
                setProducts(products.filter(item => item.id !== product.id));
              }}key='delete'/>]} type='inner' style={{width: '100%'}}>
              <p className='font-inter font-bold text-xl' style={{margin: '0'}}>{product.name}</p>
              <p className='font-inter font-normal text-l' style={{margin: '0'}}>R${product.price}</p>
              </Card>
            ))}
          </div>
        </div>
        ):( 
        <div style={{height: '100vh'}}>
          <div className='flex flex-col items-center justify-center text-left'>
            {products.length === 0 && productCreator === false? <Empty /> : products.map((product) => (
              <Card className='mt-5' type='inner' style={{width: '100%'}}>
              <p className='font-inter text-xl font-bold'style={{margin: '0'}}>{product.name}</p>
              <p className='font-inter font-normal text-l' style={{margin: '0'}}>R${product.price}</p>
              </Card>
            ))}
            <div className='mt-5 mr-auto'>
              <p className='font-inter text-xl font-bold m-0 mb-2'>Total</p>
              <p className='font-inter text-m font-normal m-0'>Volaria Revenue Fee</p>
              <p className='font-inter text-l font-bold m-0'>{
                'R$' + Math.ceil(totalPrice * 0.2)
              }</p>
              <p className='font-inter text-m font-normal m-0 mt-2'>ROBLOX Tax</p>
              <p className='font-inter text-l font-bold m-0'>{
                'R$' + Math.ceil(totalPrice - totalPrice * 0.2 * 10 / 7)
              }</p>
              <p className='font-inter text-m font-normal m-0 mt-2'>Total (Before Fees and Tax)</p>
              <p className='font-inter text-l font-bold m-0'>{
                'R$' + Math.ceil(totalPrice)
              }</p>
              <p className='font-inter text-xl font-normal m-0 mt-2'>Final Price</p>
              <p className='font-inter text-2xl font-bold m-0'>{
                'R$' + Math.ceil(totalPrice * 1.2 * 10 / 7)
              }</p>
            </div>
          </div>
        </div>)}
        <Button onClick={async () => {
          setView(finalView === true ? false : true)
          let total = 0
          for await (var product of products) {
            total += product.price
          }
          setTotalPrice(total)

          if(finalView === false) {
            toast('Keep up the good work!', {
              icon: '',
            });
          }
        }}   
          disabled={products.length === 0} className='mt-5' type='primary'>{finalView === true ? 'Back to Editing' : 'Calculate'}</Button>
          <div style={{position: 'fixed', bottom: '0', left: '0', right: '0', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
            <p  className='font-inter text-gray-500 text-l font-light mt-auto mr-1'>Made with </p>
            <p  className='font-inter text-l font-normal mt-auto text-red-500'>  </p>
           <p className='font-inter text-gray-500 text-l font-light mt-auto ml-1'> by Chris with <a href="https://reactjs.org/">React</a></p>
          </div>
      </div>
      </div>
    </>
  );
}

export default App;
